<template>
  <div class="index">
    <h3>首页-活动报名</h3>
    <el-steps :active="active" align-center class="steps">
      <el-step title="步骤1" description="姓名"></el-step>
      <el-step title="步骤2" description="联系方式"></el-step>
      <el-step title="步骤3" description="年龄"></el-step>
      <el-step title="步骤4" description="家庭地址"></el-step>
    </el-steps>
    <div class="step-box">
      <div class="step-one" v-show="active === 0">
        <el-input v-model="user" placeholder="请输入姓名"></el-input>
      </div>
      <div class="step-two" v-show="active === 1">
        <el-input v-model="phone" placeholder="请输入联系方式"></el-input>
      </div>
      <div class="step-three" v-show="active === 2">
        <el-input v-model="age" placeholder="请输入年龄"></el-input>
      </div>
      <div class="step-four" v-show="active === 3">
        <el-input v-model="address" placeholder="请输入家庭地址"></el-input>
      </div>
      <div class="step-five" v-show="active === 4">填写信息完成</div>
    </div>
    <el-button class="next-btn" type="primary" @click="nextStep">下一步</el-button>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      active: 0,
      user: '',
      phone: '',
      age: '',
      address: ''
    };
  },
  methods: {
    nextStep () {
      this.active++;
      if (this.active > 4) {
        this.$router.push({name: 'success'});
      }
    }
  }
};
</script>

<style lang="sass">
  .index
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
  .steps
    width: 100%
  .step-box
    margin: 30px 0 0
  .next-btn
    margin: 30px auto 0
</style>



