<template>
  <div class="hello">
    <img class="logo" src="../assets/welcome.png">
    <h1>{{ msg }}</h1>
    <div class="button-group">
      <router-link to="/register">
        <el-button class="button-item" type="primary">注册</el-button>
      </router-link>
      <router-link to="/login">
        <el-button class="button-item">登录</el-button>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'welcome',
  data () {
    return {
      msg: 'Welcome to The Couple App'
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass">
  .hello
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
  .logo
    display: block
    width: 218px
    height: 200px
    margin: 50px 0 0
  h1
    font-weight: normal
    margin: 100px 0
    font-size: 24px
    text-align: center
  .button-group
    display: flex
    flex-direction: row
    justify-content: space-around
    width: 100%
    .button-item
      width: 120px
</style>



